"use client";
import { useAuth } from "@/context/AuthContext";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { Plus, Search, LogOut, Bell, User, Globe } from "lucide-react";
import { useLang } from "@/context/LanguageContext";

export default function Dashboard() {
  const { user, logout, loading } = useAuth();
  const { t, toggleLang, lang } = useLang();
  const router = useRouter();

  // Mock Groups Data
  const [groups] = useState([
    { id: 1, name: "Viagem a Madrid", balance: 150.00, owe: false, image: "https://images.unsplash.com/photo-1549144511-30858b343425?auto=format&fit=crop&w=100&q=80" },
    { id: 2, name: "Cena de Viernes", balance: 45.20, owe: true, image: "https://images.unsplash.com/photo-1559339352-11d035aa65de?auto=format&fit=crop&w=100&q=80" },
    { id: 3, name: "Alquiler Piso", balance: 0, owe: false, status: "Estás al día", image: "https://images.unsplash.com/photo-1502672260266-1c1ef2d93688?auto=format&fit=crop&w=100&q=80" },
  ]);

  useEffect(() => {
    if (!loading && !user) {
      router.push("/login");
    }
  }, [user, loading, router]);

  if (loading || !user) return <div className="flex-center" style={{ height: "100vh" }}>Loading...</div>;

  return (
    <div className="flex-col gap-6 animate-in" style={{ paddingBottom: 80 }}>
      {/* Header */}
      <div className="flex-between">
        <div className="flex-center gap-2">
          <img src={user.photoURL || "https://github.com/shadcn.png"} alt="User" className="avatar" />
          <div>
            <p className="text-sm">Hola, {user.displayName?.split(" ")[0]}</p>
            <h3>SplitApp</h3>
          </div>
        </div>
        <div className="flex-center gap-4">
          <button onClick={toggleLang} style={{ background: "transparent", border: "none", color: "var(--text-muted)", fontSize: "0.8rem", cursor: "pointer" }} className="flex-center gap-1">
            <Globe size={16} /> {lang.toUpperCase()}
          </button>
          <Bell size={24} className="text-muted" />
          <User size={24} className="text-muted" onClick={logout} />
        </div>
      </div>

      {/* Balance Cards */}
      <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 16 }}>
        <div className="card flex-col gap-2" style={{ background: "rgba(6, 182, 212, 0.1)", border: "1px solid rgba(6, 182, 212, 0.2)" }}>
          <span className="text-sm text-primary">{t('owed')}</span>
          <h2 className="text-primary">$345.50</h2>
          <span className="text-sm text-success">↗ +12%</span>
        </div>
        <div className="card flex-col gap-2" style={{ background: "rgba(239, 68, 68, 0.1)", border: "1px solid rgba(239, 68, 68, 0.2)" }}>
          <span className="text-sm text-danger">{t('owe')}</span>
          <h2 className="text-danger">$120.00</h2>
          <span className="text-sm text-danger">↘ 5 facturas</span>
        </div>
      </div>

      {/* Groups Section */}
      <div>
        <div className="flex-between" style={{ marginBottom: 16 }}>
          <h2>{t('dashboard_title')}</h2>
          <a href="#" className="text-sm text-primary">{t('view_all')}</a>
        </div>

        <div className="flex-col gap-4">
          {groups.map((group) => (
            <div key={group.id} className="card flex-between" style={{ padding: 16, cursor: "pointer" }} onClick={() => router.push(`/group/${group.id}`)}>
              <div className="flex-center gap-4">
                <img src={group.image} alt={group.name} style={{ width: 60, height: 60, borderRadius: 12, objectFit: "cover" }} />
                <div>
                  <h3 style={{ fontSize: "1.1rem" }}>{group.name}</h3>
                  {group.status ? (
                    <p className="text-muted">{group.status}</p>
                  ) : (
                    <p style={{ color: group.owe ? "var(--danger)" : "var(--primary)" }}>
                      {group.owe ? `${t('owe')} $${group.balance}` : `${t('owed')} $${group.balance}`}
                    </p>
                  )}
                </div>
              </div>
              <div className="flex-center">
                {/* Avatars placeholder */}
                <div style={{ display: "flex", marginLeft: 10 }}>
                  {[1, 2].map(i => (
                    <div key={i} className="avatar" style={{ width: 24, height: 24, marginLeft: -10, border: "2px solid var(--bg-card)" }} />
                  ))}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* FAB */}
      <button
        className="btn btn-primary"
        style={{
          position: "fixed",
          bottom: 24,
          right: 24,
          width: 56,
          height: 56,
          borderRadius: "50%",
          padding: 0,
          boxShadow: "var(--shadow-glow)"
        }}
      >
        <Plus size={28} />
      </button>
    </div>
  );
}
